

















import Vue from "vue";
import { Component } from "vue-property-decorator";
@Component({})
export default class Sidenav extends Vue {
  default_sideitems = [
    {
      name: "My Profile",
      premission: "user",
      route: "/profile",
    },
    {
      name: "Security",
      premission: "adminstrator",
      route: "/security",
    },
    {
      name: "Log in",
      premission: "guest",
      route: "/log-in",
    },
    {
      name: "Register",
      premission: "guest",
      route: "/register",
    },
    {
      name: "Log out",
      premission: "user",
      route: "/log-out",
    },
  ];
  $route: any;
  $router: any;
  $store: any;

  get sideItems() {
    let list: any[] = [];
    //TODO FIX, can only get out items with either user or guest, not moderator or adminstrator. please fix
    if (this.user) {
      this.user.roles.forEach((role: string) => {
        const listToAdd = this.default_sideitems.filter(
          (item: { name: string; premission: string; route: string }) => {
            return item.premission === role || item.premission === "all";
          }
        );
        list = list.concat(listToAdd);
      });
    } else list = [];
    return list;
  }

  get user() {
    return this.$store.state.currentUser;
  }

  get sidenavState() {
    return this.$store.state.sidenavState;
  }

  get sidenavWidth() {
    return {
      width: this.sidenavState ? "18vw" : "0",
    };
  }

  goToRoute(route: string): void {
    this.$store.commit("toggleSidenav");
    if (this.$route.path === route) return;
    if (route === "/log-out") {
      this.$store.dispatch("logoutUser");
      return;
    }
    this.$router.push(route);
  }

  closeSidenav() {
    this.$store.commit("toggleSidenav");
  }
}
