




















import Vue from "vue";
import { Component } from "vue-property-decorator";

@Component({
  components: {},
})
export default class Home extends Vue {
  $store: any;
  $router: any;
  get subForumData() {
    return this.$store.state.subForums;
  }

  created() {
    this.$store.dispatch("fetchSubForums");
  }

  goToRoute(route: string) {
    this.$router.push(`/forum/${route}`);
  }
}
